import React, {Component} from 'react';
import Nav from './components/nav/nav.component';
import Logo from './components/logo/logo.component.jsx';
import ImageLinkForm from './components/imageLink/image-link.component.jsx';
import Rank from './components/rank/rank.component.jsx';
import Particles from 'react-particles-js';
import Clarifai from 'clarifai';
import FaceRecognition from './components/face-recognition/face-recognition.component.jsx';
import Footer from './components/footer/footer.component.jsx';
import SignIn from './components/sign-in/sign-in.component';
import SignUp from './components/sign-up/sign-up.component';
import CelebFace from './components/celeb-recognition/celeb-recognition';
import CelebLinkForm from './components/celeb-link/celeb-link.component';

import './App.css';

const particleOptions = {
  particles: {
      number: {
        value: 100,
        density: {
          enable: true,
          value_area: 800
        }
      }
    }
  }

const app = new Clarifai.App ({
  apiKey: '0f20ff1777e1469c8074b651bafdf863'
});


class App extends Component {
  
  constructor() {
    super();

    this.state = {
      input: '',
      imageUrl: '',
      celebUrl: '',
      box2: {},
      box: {},
      route: 'signin',
      isSignedIn: false,
      user: {
        id: '',
        name: '',
        email: '',
        entries: 0,
        joined: ''
      }
    }
  }

  loadUser = (data) => {
    this.setState({user: {
      id: data.id,
      name: data.name,
      email: data.email,
      entries: data.entries,
      joined: data.joined
    }})
  }

  calculateFaceLocation = (data) => {
    const clarifaiFace = data.outputs[0].data.regions[0].region_info.bounding_box;
    const image = document.getElementById('inputimage');
    const width = Number(image.width);
    const height = Number(image.height);
    

      return {
        leftCol: clarifaiFace.left_col * width,
        topRow: clarifaiFace.top_row * height,
        rightCol: width - (clarifaiFace.right_col * width),
        bottomRow: height - (clarifaiFace.bottom_row * height)
      }
  }

  calculateFaceLocationDisplayName = (data) => {
    const clarifaiFace = data.outputs[0].data.regions[0].region_info.bounding_box;
    const clarifaiName = data.outputs[0].data.regions[0].data.face.identity.concepts[0];
    const image = document.getElementById('inputimage2');
    const width = Number(image.width);
    const height = Number(image.height);

    return {
      leftCol: clarifaiFace.left_col * width,
      topRow: clarifaiFace.top_row * height,
      rightCol: width - (clarifaiFace.right_col * width),
      bottomRow: height - (clarifaiFace.bottom_row * height),
      celebName: clarifaiName.name,
    }
  }


  displayFaceBox = (box) => {
    this.setState({ box: box });
  }

  displayCelebBox = (box2) => {
    this.setState({ box2: box2 });
  }


  onInputChange = (event) => {
    this.setState({ input: event.target.value });
  }

  onCelebInputChange = (event) => {
    this.setState({ input: event.target.value });
  }

  onButtonSubmit = () => {
    this.setState({imageUrl: this.state.input});
    app.models
      .predict(
        Clarifai.FACE_DETECT_MODEL,
        this.state.input)
      .then(response => {
        if (response) {
          fetch('http://localhost:5000/image', {
            method: 'put',
            headers: {'Content-Type': 'application/json'},
            body: JSON.stringify({
              id: this.state.user.id
            })
          })
            .then(response => response.json())
            .then(count => {
              this.setState(Object.assign(this.state.user, { entries: count }))
            })

        }
        this.displayFaceBox(this.calculateFaceLocation(response))
      })
      .catch(err => console.log(err));
  }

  // onCelebSubmit = () => {
  //   this.setState({ celebUrl: this.state.input });
  //   app.models.predict(Clarifai.CELEBRITY_MODEL, this.state.input)
  //   .then(response => {
  //     if (response) {
  //       fetch('http://localhost:5000/image', {
  //         method: 'put',
  //         headers: {'Content-Type': 'application/json'},
  //         body: JSON.stringify({
  //           id: this.state.user.id
  //         })
  //       })
  //       .then(response => response.json())
  //       .then(count => {
  //         this.setState(Object.assign(this.state.user, { entries: count }))
  //       })
  //     }
  //     this.displayCelebBox(this.calculateFaceLocationDisplayName(response));
  //     console.log(response.outputs[0].data.regions[0].data.face.identity.concepts[0].name);
  //   })
  //   .catch(err => console.log(err));
  // }

  onRouteChange = (route) => {

    if (route === 'signout') {
      this.setState({ isSignedIn: false })
    } else if (route === 'home') {
      this.setState({ isSignedIn: true })
    }
    this.setState({ route: route });
  }

  render() {
    const { isSignedIn, imageUrl, route, box, celebUrl, box2 } = this.state;
    return (
      <div>
        
        <Particles className='particles' params={particleOptions} />

          <Nav isSignedIn={isSignedIn} onRouteChange={this.onRouteChange} />
          <div className='container'>
          { route === 'home' 
          ? 
          <div className='row'>
              <Logo />
              <Rank name={this.state.user.name} entries={this.state.user.entries}/>
              <ImageLinkForm onInputChange={this.onInputChange} onButtonSubmit={this.onButtonSubmit}/>
                <FaceRecognition box={box} imageUrl={imageUrl}/>
            <br /><br />
            <CelebLinkForm onInputChange={this.onCelebInputChange} onCelebSubmit={this.onCelebSubmit} />
              <CelebFace box2={box2} celebURl={celebUrl} />


          </div>
          : (
            route === 'signin' ?
            <SignIn loadUser={this.loadUser} onRouteChange={this.onRouteChange}/>
            :
            <SignUp loadUser={this.loadUser} onRouteChange={this.onRouteChange}/>
          )
          
          
          }
        </div>

        <Footer />
      </div>
    );
  }
  
}



export default App;
