import React from 'react';
import Logo from '../logo/logo.component.jsx';


class SignUp extends React.Component {
    constructor() {
        super();

        this.state = {
            email: '',
            password: '',
            name: ''
        }
    }

    onNameChange = (event) => {
        this.setState({name: event.target.value})
      }
    
      onEmailChange = (event) => {
        this.setState({email: event.target.value})
      }
    
      onPasswordChange = (event) => {
        this.setState({password: event.target.value})
      }
    
      onSubmitSignUp = () => {
        fetch('http://localhost:5000/signup', {
          method: 'post',
          headers: {'Content-Type': 'application/json'},
          body: JSON.stringify({
            email: this.state.email,
            password: this.state.password,
            name: this.state.name
          })
        })
          .then(response => response.json())
          .then(user => {
            if (user) {
              this.props.loadUser(user)
              this.props.onRouteChange('home');
            }
          })
      }

        render() {
            const { onRouteChange } = this.props;
            return (
                <div className='container'>
                    <Logo />
                    <br />
                    <div className='row'>
                        <div className='card'>
                            <div className='card-content'>
                                <div className='card-title center deep-purple-text'>WANT TO DETECT FACES? SIGN UP!</div>
                                    <div className='row'>
                                        <div className='col s12'>
                                            <div className="row">
                                                <div className="input-field col s12">
                                                    <i className="material-icons prefix deep-purple-text">person</i>
                                                    <input id="name" type="text" name='name' onChange={this.onNameChange} className="validate" />
                                                    <label htmlFor="display_name">DISPLAY NAME</label>
                                                </div>
                                            </div>
                                            <div className='row'>
                                                <div className='input-field col s12'>
                                                    <i className="material-icons prefix deep-purple-text">email</i>
                                                    <input id='email' type='email' name='email' onChange={this.onEmailChange} className='validate' />
                                                    <label htmlFor='password1'>ENTER EMAIL</label>
                                                </div>
                                            </div>
                                            <div className='row'>
                                                <div className='input-field col s12'>
                                                    <i className="material-icons prefix deep-purple-text">lock</i>
                                                    <input id='password' type='password' name='password' onChange={this.onPasswordChange} className='validate' />
                                                    <label htmlFor='password'>PASSWORD</label>
                                                </div>
                                            </div>
                                            <div className='row'>
                                                <div className='col s12 center'>
                                                    <button className='btn deep-purple' onClick={this.onSubmitSignUp}>SIGN UP!</button>
                                                    <br/><br/>
                                                    <p className='deep-purple-text'>ALREADY HAVE AN ACCOUNT?</p><a style={{ cursor: 'pointer'}} onClick={() => onRouteChange('signin')}>SIGN IN!</a>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                            </div>
                        </div>
                    </div>
                </div>
            )
        }
        
}

export default SignUp;