import React from 'react';
import Logo from '../logo/logo.component.jsx';


class SignIn extends React.Component {
   constructor(props) {
       super(props);

       this.state = {
           signInEmail: '',
           signInPassword: ''
       }
   }

   onEmailChange = (event) => {
    this.setState({signInEmail: event.target.value})
  }

  onPasswordChange = (event) => {
    this.setState({signInPassword: event.target.value})
  }

  onSubmitSignIn = () => {
    fetch('https://localhost:5000/signin', {
      method: 'post',
      headers: {'Content-Type': 'application/json'},
      body: JSON.stringify({
        email: this.state.signInEmail,
        password: this.state.signInPassword
      })
    })
      .then(response => response.json())
      .then(user => {
        if (user.id) {
          this.props.loadUser(user)
          this.props.onRouteChange('home');
        }
      })
  }

        render() {
            const { onRouteChange } = this.props;

            return (
                <div className='container'>
                    <Logo />
                    <br />
                    <div className='row'>
                            <div className='card'>
                                <div className='card-content'>
                                    <div className='card-title center deep-purple-text'>SIGN IN</div>
                                        <div className='row'>
                                            <div className='col s12'>
                                                <div className="row">
                                                    <div className="input-field col s12">
                                                    <i className="material-icons prefix deep-purple-text">email</i>
                                                        <input id="email-address" name='email-address' type="email" className="validate" onChange={this.onEmailChange}/>
                                                        <label htmlFor="email-address">EMAIL</label>
                                                    </div>
                                                    
                                                </div>
                                                <div className='row'>
                                                    <div className='input-field col s12'>
                                                        <i className="material-icons prefix deep-purple-text">lock</i>
                                                        <input id='password' type='password' name='password' onChange={this.onPasswordChange} className='validate' />
                                                        <label htmlFor='password'>PASSWORD</label>
                                                    </div>
                                                </div>
                                                <div className='row'>
                                                    <div className='col s12 center'>
                                                        <button className='btn deep-purple' type='submit' onClick={this.onSubmitSignIn}>SUBMIT</button>
                                                        <br/><br/>
                                                        <p className='deep-purple-text'>DON'T HAVE AN ACCOUNT?</p><a style={{ cursor: 'pointer'}} onClick={() =>onRouteChange('signup')}>SIGN UP!</a>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                </div>
                            </div>
                    </div>
                </div>
            )
        }
}

export default SignIn;